.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  text-align: center;
}

.actions {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  gap: 4px;
}
.actions>button {
  cursor: pointer;
}

.rc-table {
  margin: 10px;
  width: 500px;
  border-collapse: collapse;
}

.row:hover {
  background-color: aqua;
}
.row.selected {
  background-color: red;
}

.col {
  border: 1px solid grey;
  padding: 2px 4px;
}

.col-3,.col-4 {
  text-align: center;
  cursor: pointer;
}
